html {
	color-scheme: dark light;
	font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
	max-width: 100vw;
	box-sizing: border-box;
}

* {
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

.summary-cards {
	display: inline-block;
	max-width: 100%;
	width: auto;
	width: fit-content;

	img:first-child {
		width: 100%;
	}

	img {
		width: 50%;
		border: 0;
		float: left;
		margin: 0;
		padding: 5px;
	}
}
